.header {
  width: 100%;
  z-index: 9999;
  background-color: #153661;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  color: white;
  margin-right: 1.5;
}

.ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: baseline;
}

.li {
  margin-right: 1.5rem;

}

.a {
  text-decoration: none;
  color: white;
  font-weight: bold;
}

.button {
  font: inherit;
  background-color: transparent;
  border: 1px solid white;
  color: white;
  font-weight: bold;
  padding: 0.5rem 1.5rem;
  border-radius: 6px;
  cursor: pointer;
}

.a:hover {
  color: #c291e2;
}

.button:hover {
  background-color: white;
  color: #153661;
}

.NavDropdownLink {
  text-decoration: none;
  color: #212529;
  padding-left: 0.5rem;
}

.NavDropdownLink a:hover, 
.NavDropdownLink a:focus, 
.NavDropdownLink a:active {
  color: white;
}

.navDropDown {
  background-color: #153661;
}